import { FC } from "react";
import { Img, ImgCard, Label, Wrapper } from "./styles-contact-name-img";
import { ContactNameImgProps } from "./types-contact-name-img";

const ContactNameImg: FC<ContactNameImgProps> = ({icon,label}) => {
    return (
      <Wrapper>
        <ImgCard>
          <Img src={icon} />
        </ImgCard>
        <Label>{label}</Label>
      </Wrapper>
    );
}

export default ContactNameImg