import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import { FC } from 'react';
import { ContactMock } from '@src/mocks';
import { ContactNameImg } from '..';
import { Card, Wrapper } from './styles-outsource';
import { VerticalSeparator } from '../separators/sepatators';

const Outsource: FC<{}> = () => {
  return (
    <Wrapper>
      <FormControl>
        <RadioGroup
          aria-labelledby="demo-radio-buttons-group-label"
          defaultValue="female"
          name="radio-buttons-group"
        >
          {ContactMock.map((item, index) => (
            <>
              <VerticalSeparator height="15px" />
              <Card key={item.id}>
                <ContactNameImg icon={item.icon} label={item.fullName} />
                <FormControlLabel
                  value={item.fullName}
                  control={<Radio />}
                  label=""
                />
              </Card>
            </>
          ))}
        </RadioGroup>
      </FormControl>
    </Wrapper>
  );
};

export default Outsource;
