import { Button, UniversalModal } from "@components/common";
import { FC } from "react";
import { ButtonBox, Wrapper } from "./styles-remove-modal";
import { RemoveModalProps } from "./types-remove-modal";
import trashIcon from "@assets/images/dashboard/chat/trashModal.svg";
const RemoveModal: FC<RemoveModalProps> = ({title, open, handleClose }) => {
  return (
    <UniversalModal open={open} handleClose={handleClose}>
      <Wrapper>
        <img src={trashIcon} alt="trash icon" />
        <h1>{title}</h1>
        <ButtonBox>
          <Button
            onClick={handleClose}
            backColor="none"
            border={"#00000066"}
            color="#00000066"
            value="Bekor qilish"
            type="reset"
          />
          <Button value="O’chirish" type="submit" />
        </ButtonBox>
      </Wrapper>
    </UniversalModal>
  );
};

export default RemoveModal;
