import { styled, Tooltip, tooltipClasses, TooltipProps } from "@mui/material";
import { FC } from "react";
import { Icon, Label, Upload, WrapIcon } from "./styles-modal-top-button";
import { ModalTopButtonProps } from "./types-modal-top-button";
const BootstrapTooltip = styled(({ className, ...props }: TooltipProps) => (
  <Tooltip {...props} arrow classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.arrow}`]: {
    color: "#0496ff",
  },
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: "#0496ff",
    maxWidth: 178,
    fontSize: theme.typography.pxToRem(12),
  },
}));
const ModalTopButton: FC<ModalTopButtonProps> = ({
  icon,
  title,
  isFile,
  isTooltip,
  onClick,
}) => {
  return (
    <>
      {isTooltip ? (
        <BootstrapTooltip title={isTooltip.name}>
          <Label onClick={onClick}>
            {title}
            {isFile && <Upload type="file" />}
            <WrapIcon>
              <Icon src={icon} />
              {isFile && <p>{isFile?.count}ta</p>}
            </WrapIcon>
          </Label>
        </BootstrapTooltip>
      ) : (
        <Label onClick={onClick}>
          {title}

          {isFile && <Upload type="file" />}
          <WrapIcon>
            <Icon src={icon} />
            {isFile && <p>{isFile?.count}ta</p>}
          </WrapIcon>
        </Label>
      )}
    </>
  );
};

export default ModalTopButton;
