import { FC } from "react";
import { Wrapper } from "./styles-participant";
import { ParticipantProps } from "./types-participant";
import closeIcon from "@assets/images/dashboard/chat/close.svg";
const Participant: FC<ParticipantProps> = ({ participantData }) => {
  return (
    <Wrapper>
      <img src={participantData.avatar} alt="avatar" />
      <p>{participantData.name}</p>
      <img src={closeIcon} alt="icon" />
    </Wrapper>
  );
};

export default Participant;
