import styled from "styled-components";

export const Wrapper = styled.div`
  position: relative;
  margin-top: 23px;

  img {
    position: absolute;
    right: 5px;
    top: 55%;
  }

  .css-78me8v-MuiFormControl-root {
    margin: 0 !important;
    margin-top: 15px !important;
  }

  // delete input label
  .css-1in441m {
    display: none;
  }

  // input border
  .css-1d3z3hw-MuiOutlinedInput-notchedOutline {
    :active {
      border: 1px solid #0496ff;
    }
  }

  // delete input dropdown svg icon
  .MuiSvgIcon-root {
    display: none;
  }
`;

export const CustomChip = styled.div`
  position: relative;
  padding: 1px 5px;
  background: #0496ff;
  border-radius: 4px;
  color: #fff;
  display: flex;
  align-items: center;
  gap: 7px;
  padding-left: 26px;

  img {
    position: absolute;
    left: 7px;
    top: 4px;
  }
`;

export const Title = styled.h1`
  font-size: 16px;
  font-weight: 500;
  color: #000000;
`;
