import { FC, useState } from "react";
import { GeneralContainerProps } from "./types-general-container";
import { Box, ContentCard, SidebarCard, Wrapper } from "./styles-general-container";
import Sidebar from "../side-bar/sidebar";
import Navbar from "../nav-bar/navbar";

const GeneralContainer: FC<GeneralContainerProps> = ({children}) =>{
    const [navbarActive, setNavbarActive] = useState<number>(0)
    return (
      <Wrapper>
        <SidebarCard>
          <Sidebar
            activeState={navbarActive}
            setActiveState={setNavbarActive}
          />
        </SidebarCard>
        <ContentCard>
          <Navbar activeState={navbarActive} setActiveState={setNavbarActive} />
          <Box>{children}</Box>
        </ContentCard>
      </Wrapper>
    );
}

export default GeneralContainer