import { FC } from 'react';
import {
  AccountCard,
  Avatar,
  AvatarCard,
  Box,
  Card,
  Img,
  Label,
  Name,
  RadioCard,
  VerticalLine,
  Wrapper,
} from './styles-structure';
import { StructureProps } from './types-structure';
import { ContactNameImg, DropDownMenu } from '@components/common';
import profile from '@assets/images/dashboard/profile.svg';
import vertical_dots from '@assets/images/dashboard/vertical-dots.svg';
import {
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
} from '@mui/material';
import { ContactMock } from '@src/mocks';
import { VerticalSeparator } from '../separators/sepatators';
export const dropDownMenu = [
  {
    icon: null,
    title: 'Ishchi oynasi',
  },
  {
    icon: null,
    title: 'O’zgartirish',
  },
  {
    icon: null,
    title: 'Ishdan bo’shatish',
  },
];
const StructureItemDropDown: FC<{
  onSelect: (value: string) => void;
  value: string;
  label: string;
}> = ({ onSelect, value, label }) => {
  return (
    <AccountCard>
      <AvatarCard>
        <Avatar>
          <Img src={profile} alt="profile" />
        </Avatar>
        <Box>
          <div>
            <Name>{value}</Name>
            <Label>{label}</Label>
          </div>

          <DropDownMenu
            children={<Img src={vertical_dots} alt="vertical dots" />}
            bordered={false}
            forNotification={false}
            menuData={dropDownMenu}
            onSelect={onSelect}
          />
        </Box>
      </AvatarCard>
    </AccountCard>
  );
};
const StructureItemRadio: FC<{
  onSelect: (value: string) => void;
  value: string;
  label: string;
}> = ({ onSelect, value, label }) => {
  return (
    <AccountCard>
      <RadioCard key={value}>
        <FormControlLabel value={value} control={<Radio />} label="" />
        <AvatarCard>
          <Avatar>
            <Img src={profile} alt="profile" />
          </Avatar>
          <Box>
            <div>
              <Name>{value}</Name>
              <Label>{label}</Label>
            </div>
          </Box>
        </AvatarCard>
      </RadioCard>
    </AccountCard>
  );
};

const Structure: FC<StructureProps> = ({ onSelect, forChat }) => {
  return (
    <Wrapper>
      {forChat ? (
        <FormControl>
          <RadioGroup
            aria-labelledby="demo-radio-buttons-group-label"
            defaultValue="female"
            name="radio-buttons-group"
          >
            {ContactMock.map((item, index) => (
              <Card position={index % 4}>
                <VerticalLine />
                <StructureItemRadio
                  onSelect={onSelect}
                  value={item.fullName}
                  label="it director"
                />
              </Card>
            ))}
          </RadioGroup>
        </FormControl>
      ) : (
        <>
          {ContactMock.map((item, index) => (
            <Card position={index % 4}>
              <VerticalLine />

              <StructureItemDropDown
                onSelect={onSelect}
                value={item.fullName}
                label="it director"
              />
            </Card>
          ))}
        </>
      )}
    </Wrapper>
  );
};

export default Structure;
