import { FC } from "react";
import { ButtonProps } from "./types-button";
import { Wrapper, Button as AsButton } from "./styles-button";

const Button: FC<ButtonProps> = ({
  value,
  type,
  onClick,
  icon,
  backColor,
  border,
  color,
  id,
  style
}) => {
  return (
    <Wrapper>
      <AsButton id={id} style={style} type={type} border={border} onClick={onClick} background={backColor} color={color}>
        {icon && <img src={icon} alt="icon-button" />}
        {value}
      </AsButton>
    </Wrapper>
  );
};

export default Button;
