import Box from "@mui/material/Box";
import Slider from "@mui/material/Slider";
import { FC, useState } from "react";
import { AsBox, FlexContent, Label } from "./styles-slider";
import removeIcon from "@assets/images/dashboard/chat/close.svg";
import { SliderProps } from "./types-slider";
const MuiSlider: FC<SliderProps> = ({id=0,onEdit,onRemove,value}) => {
  // const [value, setValue] = useState<number | number[]>(10);
  return (
    <AsBox>
      <Label>{value} daqiqa </Label>
      <FlexContent>
        <Slider
          defaultValue={value}
          value={value}
          aria-label="Default"
          valueLabelDisplay="auto"
          onChange={(e, value) => {
            onEdit&&onEdit(id,value)
          }}
        />
        <img src={removeIcon} onClick={()=>onRemove&&onRemove(id)} alt="icon" />
      </FlexContent>
    </AsBox>
  );
};

export default MuiSlider;
