import * as React from "react";
import Box from "@mui/material/Box";
import OutlinedInput from "@mui/material/OutlinedInput";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import checkImg from "@assets/images/common/selector/check-circle.svg";
import dropdown from "@assets/images/common/selector/dropdown.svg";
import { CustomChip, Title, Wrapper } from "./style-multi-selector";
import { MultiSelectorType } from "./types-chip-selector";

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const MultiSelector: React.FC<MultiSelectorType> = ({
  placeholder,
  checkBoxDatas,
  title,
}) => {
  const [roles, setRoles] = React.useState<string[]>([placeholder]);
  const [values, setValues] = React.useState<string[]>([]);
  const [checkBoxData, setCheckBoxData] = React.useState<any[]>(checkBoxDatas);

  React.useEffect(() => {
    if (roles.length > 1 && roles[0] === placeholder)
      setRoles(roles.filter((item) => item !== placeholder));
    else if (roles.length === 0) setRoles([placeholder]);
  }, [roles]);
  const onChange = (index: number, checked: boolean, target: any) => {
    const { value } = target;

    if (roles.includes(value)) setRoles(roles.filter((item) => item !== value));
    else setRoles([...roles, value] as string[]);

    let checkboxes = checkBoxData;
    let changeObj = checkboxes[index];
    changeObj.checked = checked;
    checkboxes[index] = changeObj;
    setCheckBoxData(checkboxes);
    if (values.includes(changeObj.value)) {
      const filtered = values.filter((item) => item !== changeObj.value);
      setValues(filtered);
    } else setValues([...values, changeObj.value]);
  };

  return (
    <Wrapper>
      <Title>{title}</Title>
      <img src={dropdown} alt="" />
      <FormControl sx={{ m: 1, width: "100%" }}>
        <Select
          labelId="demo-multiple-chip-label"
          id="demo-multiple-chip"
          multiple
          value={roles}
          input={<OutlinedInput id="select-multiple-chip" label="Chip" />}
          renderValue={(selected) => {
            if (roles[0] === placeholder && !roles[1])
              return <div>{roles[0]}</div>;
            else
              return (
                <Box sx={{ display: "flex", flexWrap: "wrap", gap: 0.5 }}>
                  {selected.map((value) => (
                    <CustomChip key={value}>
                      <img src={checkImg} alt="check img" />
                      {value}
                    </CustomChip>
                  ))}
                </Box>
              );
          }}
          MenuProps={MenuProps}
        >
          <FormGroup
            className="custom-checkboxes-form"
            style={{
              padding: "14px 10px",
              display: "grid",
              gridTemplateColumns: "1fr 1fr",
              top: "50px",
            }}
          >
            {checkBoxData.map((item, index) => (
              <FormControlLabel
                key={index}
                control={
                  <Checkbox
                    checked={item.checked}
                    style={{ color: "#0496FF" }}
                  />
                }
                label={item.value}
                //@ts-ignore
                value={item.value}
                onChange={(e, checked) => onChange(index, checked, e.target)}
              />
            ))}
          </FormGroup>
        </Select>
      </FormControl>
    </Wrapper>
  );
};

export default MultiSelector;
