import styled from "styled-components";

export const Button = styled.button`
  background: #ffffff;
  border: 1px solid #0496ff;
  border-radius: 60px;
  color: #0496ff;
  cursor: pointer;
  padding: 12px 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 170px;
  p {
    font-size: 20px;
    display: flex;
    align-items: center;
    font-weight: 500;
    span {
      margin-left: 10px;
      color: #fff;
      background: #0496ff;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 25px;
      padding: 2px 7px;
      font-size: 16px;
      font-weight: 500;
    }
  }
  height: 48px;
`;
