import { FC } from 'react';
import { PhoneInputProps } from './types-phone-input';
import 'react-phone-input-2/lib/style.css';
import { ReactPhoneInput, Wrapper } from './styles-phone-input';
import { Title } from '../input/styles-input';
import { Controller } from 'react-hook-form';

const PhoneInput: FC<PhoneInputProps> = ({ name, control }) => {
  return (
    <Wrapper>
      <Title>Telefon raqam kiriting</Title>
      <Controller
        control={control}
        name={name}
        render={({field}) => (
          <ReactPhoneInput
            country={'uz'}
            {...field}
            onChange={(data) => field.onChange(data)}
          />
        )}
      />
    </Wrapper>
  );
};

export default PhoneInput;
