import styled from "styled-components";

export const Wrapper = styled.div`
display: flex;
background:#F4F5F7;
border-radius: 7px;
max-width:210px;
width:max-content;
padding:8px 5px;
align-items: center;
justify-content: space-between;
margin:5px 7px;
img[alt="avatar"]{
    width: 24px;
    height: 24px;
    border-radius: 25px;
}
p{
    font-size: 12px;
    font-weight: 500;
    margin:0 9px;
}
img[alt="icon"]{
    cursor: pointer;
}
`