import { FC, SyntheticEvent, useState } from 'react';
import { styled } from '@mui/material/styles';
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import MuiAccordion, { AccordionProps } from '@mui/material/Accordion';
import MuiAccordionSummary, {
  AccordionSummaryProps,
} from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import { Avatar } from '@mui/material';
import { Card, Label, Time, Title, ButtonGroup } from './styles-accordion';
import { Button } from '@components/common';
import avatar from '@assets/images/dashboard/avatar.svg';

const Accordion = styled((props: AccordionProps) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  border: `1px solid ${theme.palette.divider}`,
  '&:not(:last-child)': {
    borderBottom: 0,
  },
  '&:before': {
    display: 'none',
  },
}));

const AccordionSummary = styled((props: AccordionSummaryProps) => (
  <MuiAccordionSummary
    expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }} />}
    {...props}
  />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === 'dark'
      ? 'rgba(255, 255, 255, .05)'
      : 'rgba(0, 0, 0, .03)',
  flexDirection: 'row-reverse',
  '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
    transform: 'rotate(90deg)',
  },
  '& .MuiAccordionSummary-content': {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(2),
  borderTop: '1px solid rgba(0, 0, 0, .125)',
}));

const AccordionNotification: FC<{}> = () => {
  const [expanded, setExpanded] = useState<string | false>('panel1');

  const handleChange =
    (panel: string) => (event: SyntheticEvent, newExpanded: boolean) => {
      setExpanded(newExpanded ? panel : false);
    };

  return (
    <div>
      <Accordion
        expanded={expanded === 'panel1'}
        onChange={handleChange('panel1')}
      >
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography>
            <Card>
              <Avatar src={avatar} />
              <Title>
                Ilhom Ibragimov<Label>sizni majlisga qo'shdi</Label>
                <Time>39min</Time>
              </Title>
            </Card>
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <ButtonGroup>
            <Button
              color="#F31B1B"
              type="button"
              value="Rad etish"
              onClick={() => console.log('clicked rad etish')}
            />
            <Button
              color="#06D6A0"
              type="button"
              value="Qabul qilish"
              onClick={() => console.log('clicked qabul qilish')}
            />
          </ButtonGroup>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === 'panel2'}
        onChange={handleChange('panel2')}
      >
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography>
            <Card>
              <Avatar src={avatar} />
              <Title>
                Ilhom Ibragimov<Label>sizni majlisga qo'shdi</Label>
                <Time>39min</Time>
              </Title>
            </Card>
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <ButtonGroup>
            <Button
              color="#F31B1B"
              type="button"
              value="Rad etish"
              onClick={() => console.log('clicked rad etish')}
            />
            <Button
              color="#06D6A0"
              type="button"
              value="Qabul qilish"
              onClick={() => console.log('clicked qabul qilish')}
            />
          </ButtonGroup>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === 'panel3'}
        onChange={handleChange('panel3')}
      >
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography>
            <Card>
              <Avatar src={avatar} />
              <Title>
                Ilhom Ibragimov<Label>sizni majlisga qo'shdi</Label>
                <Time>39min</Time>
              </Title>
            </Card>
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <ButtonGroup>
            <Button
              color="#F31B1B"
              type="button"
              value="Rad etish"
              onClick={() => console.log('clicked rad etish')}
            />
            <Button
              color="#06D6A0"
              type="button"
              value="Qabul qilish"
              onClick={() => console.log('clicked qabul qilish')}
            />
          </ButtonGroup>
        </AccordionDetails>
      </Accordion>
    </div>
  );
};

export default AccordionNotification;
