import { FC, useState } from "react";
import Menu from "@mui/material/Menu";
import { DropDownProps } from "./types-drop-down";
import { AsButton, AsMenuItem, Img, Title } from "./styles-drop-down";
import CustomizedAccordions from "./accordion/accordion";

const DropDownMenu: FC<DropDownProps> = ({
  children,
  bordered,
  menuData,
  forNotification,
  background,
  onSelect,
}) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div>
      <AsButton
        bordered={!!bordered}
        id="basic-button"
        aria-controls={open ? "basic-menu" : undefined}
        aria-haspopup="true"
        aria-expanded={open ? "true" : undefined}
        onClick={handleClick}
        background={background}
      >
        {children}
      </AsButton>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          "aria-labelledby": "basic-button",
        }}
      >
        {!forNotification ? (
          menuData.map((item: any, idx: number) => (
            <AsMenuItem
              key={idx}
              onClick={() => {
                onSelect(item.title);
                handleClose();
              }}
            >
              <Img
                style={idx === 0 ? { marginLeft: "-5px" } : { marginLeft: "0" }}
                src={item.icon}
              />
              <Title
                style={idx === 0 ? { color: "#0496ff" } : { color: "#000" }}
              >
                {item.title}
              </Title>
            </AsMenuItem>
          ))
        ) : (
          <CustomizedAccordions />
        )}
      </Menu>
    </div>
  );
};

export default DropDownMenu;
