import { ContactAccordion, LightButton, SearchInput } from '@components/common';
import { ContactMock } from '@src/mocks';
import { FC, useState } from 'react';
import AsAccordion from '../accordion/accordion';
import Outsource from '../outSource/outsource';
import Structure from '../structure/structure';
import {
  ContactCard,
  Header,
  Item,
  ItemCard,
  List,
  Point,
  Title,
  Wrapper,
} from './styles-partner-selection';
import { PartnerSelectionProps } from './types-partner-selection';

const PartnerSelection: FC<PartnerSelectionProps> = () => {
  const [compliance, setCompliance] = useState<string>('');
  const [open, setOpen] = useState<boolean>(false);
  const [activeState, setActiveState] = useState<number | null>(null);
  const [activeIndex, setActiveIndex] = useState<number>(0);
  const onSelect = (value: string) => {
    setActiveState(null);
    setCompliance(value);
    setOpen(true);
  };
  return (
    <Wrapper>
      <Header>
        <Title>Sherik tanlang</Title>
        <LightButton text="Saqlash" />
      </Header>
      <SearchInput
        placeholder="Qidirish"
        name="search"
        onChange={() => console.log('changing')}
      />
      <List>
        <Item active={activeIndex === 0} onClick={() => setActiveIndex(0)}>
          <ItemCard>Jamoa {activeIndex === 0 && <Point />}</ItemCard>
        </Item>
        <Item active={activeIndex === 1} onClick={() => setActiveIndex(1)}>
          <ItemCard>Ro'yxat {activeIndex === 1 && <Point />}</ItemCard>
        </Item>
        <Item active={activeIndex === 2} onClick={() => setActiveIndex(2)}>
          <ItemCard>Tuzilma {activeIndex === 2 && <Point />}</ItemCard>
        </Item>
        <Item active={activeIndex === 3} onClick={() => setActiveIndex(3)}>
          <ItemCard>Outsource {activeIndex === 3 && <Point />}</ItemCard>
        </Item>
      </List>
      <ContactCard>
        {
        
           activeIndex === 2 ?<AsAccordion onSelect={onSelect} forChat/> : <Outsource/>
        }
      </ContactCard>
    </Wrapper>
  );
};

export default PartnerSelection;
