import { FC, useState } from 'react';
import ruLocale from 'date-fns/locale/ru';
import TextField from '@mui/material/TextField';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { Wrapper } from './styles-date-input';
import { DateInputProps } from './types-date-input';
import { Title } from '../input/styles-input';
import { DateTimePicker } from '@mui/x-date-pickers';

const DateInput: FC<DateInputProps> = ({title,forAddStaff=false}) => {
  const [value, setValue] = useState<Date | null>(new Date());

  return (
    <Wrapper forAddStaff={forAddStaff}>
      {!!title && <Title>{title}</Title>}
      <LocalizationProvider dateAdapter={AdapterDateFns} locale={ruLocale}>
      <DateTimePicker
          mask={'__.__.____'}
          value={value}
          onChange={(newValue) => setValue(newValue)}
          renderInput={(params) => <TextField {...params} />}
        />
      </LocalizationProvider>
    </Wrapper>
  );
};

export default DateInput;
