import { FC, useState } from "react";
import {
  Button,
  Input,
  LightButton,
  LightSelect,
  Selector,
  ModalTopButton,
  UniversalModal,
  UploadFile,
} from "@components/common";
import { OpenTaskForm, OpenTaskModalProps } from "./types-open-task-modal";
import {
  AddToCalendar,
  BottomButtonCard,
  BottomSelectorCard,
  ButtonCard,
  Icon,
  RepetitionBox,
  SelectorWrap,
  SubmitButtonBox,
  TaskNameBox,
  Title,
  Wrapper,
} from "./styles-open-task-modal";
import StartToFinish from "../start-to-finish/start-to-finish";
import closeIcon from "@assets/images/dashboard/chat/close.svg";
import addPerformer from "@assets/images/dashboard/chat/addPerformer.svg";
import LeaderIcon from "@assets/images/dashboard/chat/leader.svg";
import authorIcon from "@assets/images/dashboard/chat/author.svg";
import statusIcon from "@assets/images/dashboard/chat/status.svg";
import statusNew from "@assets/images/meetings/status-new.svg";
import person from "@assets/images/dashboard/person.svg";
import sawIcon from "@assets/images/dashboard/chat/eyeBlue.svg";
import { useForm } from "react-hook-form";
import { styled, Switch, SwitchProps } from "@mui/material";
import { CloseButton } from "../open-assembly/styles-open-assembly";
import StatusBtn from "../status-btn/status-btn";
const OpenTaskModal: FC<OpenTaskModalProps> = ({ open, handleClose }) => {
  const [taskName, setTaskName] = useState<string>("");
  const [project, setProject] = useState<string>("");
  const [repeatValue, setRepeatValue] = useState<string>("");
  const btnDatas = [
    { icon: addPerformer, title: "Ijrochi" },
    { icon: LeaderIcon, title: "Rahbar" },
    { icon: authorIcon, title: "Muallif" },
    { icon: statusIcon, title: "Status" },
  ];
  const { handleSubmit, control } = useForm<OpenTaskForm>({
    defaultValues: {
      meetingName: "",
      comment: "",
      meetingPlace: "",
    },
  });
  const IOSSwitch = styled((props: SwitchProps) => (
    <Switch
      focusVisibleClassName=".Mui-focusVisible"
      disableRipple
      {...props}
    />
  ))(({ theme }) => ({
    width: 42,
    height: 26,
    padding: 0,
    "& .MuiSwitch-switchBase": {
      padding: 0,
      margin: 2,
      transitionDuration: "300ms",
      "&.Mui-checked": {
        transform: "translateX(16px)",
        color: "#fff",
        "& + .MuiSwitch-track": {
          backgroundColor:
            theme.palette.mode === "dark" ? "#2ECA45" : "#65C466",
          opacity: 1,
          border: 0,
        },
        "&.Mui-disabled + .MuiSwitch-track": {
          opacity: 0.5,
        },
      },
      "&.Mui-focusVisible .MuiSwitch-thumb": {
        color: "#33cf4d",
        border: "6px solid #fff",
      },
      "&.Mui-disabled .MuiSwitch-thumb": {
        color:
          theme.palette.mode === "light"
            ? theme.palette.grey[100]
            : theme.palette.grey[600],
      },
      "&.Mui-disabled + .MuiSwitch-track": {
        opacity: theme.palette.mode === "light" ? 0.7 : 0.3,
      },
    },
    "& .MuiSwitch-thumb": {
      boxSizing: "border-box",
      width: 22,
      height: 22,
    },
    "& .MuiSwitch-track": {
      borderRadius: 26 / 2,
      backgroundColor: theme.palette.mode === "light" ? "#E9E9EA" : "#39393D",
      opacity: 1,
      transition: theme.transitions.create(["background-color"], {
        duration: 500,
      }),
    },
  }));

  return (
    <UniversalModal open={open} handleClose={handleClose}>
      <Wrapper>
        <Title>Vazifa qo'shish</Title>
        <CloseButton onClick={handleClose}>
          <img src={closeIcon} alt="icon" />
        </CloseButton>{" "}
        <ButtonCard>
          <ModalTopButton
            title="Ijrochi"
            icon={addPerformer}
            onClick={() => console.log("clicked author")}
          />
          <ModalTopButton
            title="Rahbar"
            icon={LeaderIcon}
            isTooltip={{ name: "Jahongir Majidov" }}
            onClick={() => console.log("clicked author")}
          />
          <ModalTopButton
            title="Muallif"
            icon={person}
            isTooltip={{ name: "Jahongir Majidov" }}
            onClick={() => console.log("clicked author")}
          />
          <ModalTopButton
            title="Status"
            icon={statusNew}
            onClick={() => console.log("clicked status")}
          />
        </ButtonCard>
        <TaskNameBox>
          <Input
            onChange={() => console.log("clicked")}
            placeholder="Vazifa nomi"
            name="vazifa"
            title=""
            control={control}
          />
        </TaskNameBox>
        <StartToFinish />
        <BottomButtonCard>
          <SelectorWrap>
            <Selector
              options={[
                "Bir marta",
                "Har kuni",
                "Har hafta",
                "Har oy",
                "Har yil",
              ]}
              placeholder="Proyekt nomi"
              selectedValue={setProject}
              marginTop="10px"
              defaultValue={project}
              withClearIcon
              isAutoComplete={false}
            />
          </SelectorWrap>
          <SelectorWrap>
            <Selector
              options={[
                "Bir marta",
                "Har kuni",
                "Har hafta",
                "Har oy",
                "Har yil",
              ]}
              placeholder="Vazifa nomi"
              selectedValue={setTaskName}
              marginTop="10px"
              defaultValue={'Har kuni'}
              withClearIcon
              isAutoComplete={false}
            />
          </SelectorWrap>
        </BottomButtonCard>
        <BottomSelectorCard></BottomSelectorCard>
        <RepetitionBox>
          <Selector
            options={["Bir marta", "Ko`p marta"]}
            placeholder=" "
            selectedValue={setRepeatValue}
            marginTop="0px"
            defaultValue="Bir marta"
            withClearIcon
          />
        </RepetitionBox>
        <AddToCalendar>
          <Title>Kalendarga qo’shish</Title>
          <IOSSwitch sx={{ m: 1 }} defaultChecked />
        </AddToCalendar>
        <SubmitButtonBox>
          <Button value="Saqlash" type="submit" />
        </SubmitButtonBox>
      </Wrapper>
    </UniversalModal>
  );
};

export default OpenTaskModal;
