import { FC, useEffect, useState } from "react";
import Autocomplete from "@mui/material/Autocomplete/Autocomplete";
import TextField from "@mui/material/TextField/TextField";
import { SelectorProps } from "./types-selector";
import { Title, Wrapper } from "./styles-selector";
import DropDownIcon from "@assets/images/common/selector/dropdown.svg";

// basic selector
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select, { SelectChangeEvent } from "@mui/material/Select";

const Selector: FC<SelectorProps> = ({
  title,
  options,
  placeholder,
  selectedValue,
  marginTop,
  defaultValue,
  withClearIcon,
  isAutoComplete,
  Style,
}) => {
  const [value, setValue] = useState(defaultValue);

  // basic selector
  const handleChange = (event: SelectChangeEvent) => {
    // setAge(event.target.value as string);
    let value = event.target.value;
    selectedValue(value);
    setValue(value);
  };

  useEffect(() => {
    setValue(defaultValue);
  }, [defaultValue]);

  return (
    <Wrapper marginTop={marginTop}>
      {!!title && <Title>{title}</Title>}
      {isAutoComplete ? (
        <Autocomplete
          popupIcon={<img src={DropDownIcon} alt="Drop Down" />}
          disablePortal
          clearIcon={!withClearIcon}
          id="combo-box-demo"
          value={value}
          onChange={(event: any, value: any) => {
            selectedValue(value);
            setValue(value);
          }}
          options={options}
          sx={{ width: 300 }}
          renderInput={(params) => (
            <TextField {...params} placeholder={placeholder} />
          )}
        />
      ) : (
        <FormControl className="basic-selector" fullWidth>
          <img src={DropDownIcon} alt="Drop Down" />
          <Select
            id="demo-simple-select"
            value={value}
            onChange={handleChange}
            placeholder={placeholder}
            defaultValue={defaultValue}
            style={Style}
          >
            {options.map((item: any) => (
              <MenuItem disabled={item === options[0]} key={item} value={item}>
                {item}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      )}
    </Wrapper>
  );
};

export default Selector;
