import { FC, useState } from 'react';
import {
  Icon,
  Label,
  MenuItem,
  Position,
  Profile,
  Title,
  Wrapper,
} from './styles-navbar';
import { NavbarProps } from './types-navbar';

import arrow from '@assets/images/dashboard/down-arrow.svg';
import avatar from '@assets/images/dashboard/avatar.svg';
import { Avatar } from '@mui/material';
import { navbarAssets, navbarAssetsWithLabel } from '../../../data/data';
import {
  AddMenu,
  EditMenu,
  LanguageMenu,
  ProfileMenu,
} from '../../../data/drop-down-data';
import { DropDownMenu } from '@src/components/common';
import { useHistory } from 'react-router-dom';

const AsProfile = () => {
  return (
    <Profile>
      <Avatar src={avatar} style={{ marginRight: '16px' }} />
      <div>
        <Title>
          Ibrohim Gulyamov <Icon src={arrow} />
        </Title>
        <Position>Biznes egasi</Position>
      </div>
    </Profile>
  );
};

const Navbar: FC<NavbarProps> = ({ activeState, setActiveState }) => {
  const [dropDownstate, setDropDownState] = useState<number>(-1);
  const history = useHistory();
  const Route = (index: number) => {
    switch (index) {
      case 0:
        return history.push('/target-map');
      case 1:
        return history.push('/dashboard');
      case 2:
        return history.push('/search');
      case 3:
        return history.push('/perfect');
      case 4:
        return history.push('/premium');
      default:
        return history.push('/companies');
    }
  };

  const onSelect = (value: string) => {
    console.log(value);
    
  };
  return (
    <Wrapper>
      {navbarAssetsWithLabel.map((item, index) => (
        <MenuItem
          key={index}
          forPremium={index === 4}
          active={activeState - 6 === index && index !== 3}
          onClick={() => {
            Route(index);
            index !== 3 && setActiveState(index + 6);
          }}
        >
          <Icon
            src={index === activeState - 6 ? item.active_icon : item.icon}
          />
          {<Label active={index === 3}>{item.label}</Label>}
        </MenuItem>
      ))}
      {navbarAssets.map((item, index) => (
        // <MenuItem2 bordered={item.bordered} onClick={()=>setDropDownState(index)}>
        //   {item.icon ? <Icon src={item.icon} /> : 'UZ'}
        //   {item.notification && <Notification>19</Notification>}
        //   <DropDown open={index === dropDownstate}/>
        // </MenuItem2>
        <DropDownMenu
          children={
            item.icon && item.icon !== 'profile' ? (
              <Icon src={item.icon} />
            ) : item.icon === 'profile' ? (
              <AsProfile />
            ) : (
              'UZ'
            )
          }
          key={index}
          bordered={item.bordered}
          forNotification={index === 3}
          onSelect={onSelect}
          menuData={
            index === 0
              ? EditMenu
              : index === 1
              ? LanguageMenu
              : index === 2
              ? AddMenu
              : index === 4
              ? ProfileMenu
              : EditMenu
          }
        />
      ))}
    </Wrapper>
  );
};

export default Navbar;
