import { FC, useState } from 'react';
import { useForm } from 'react-hook-form';
import { FormControlLabel, Switch } from '@mui/material';
import { TaskOpeningForm, TaskOpeningProps } from './types-task-opening';
import {
  Input,
  LightButton,
  LightSelect,
  PartnerSelection,
  Selector,
  UniversalModal,
  UploadFile,
} from '@components/common';
import {
  HorizontalSeparator,
  VerticalSeparator,
} from '@components/common/separators/sepatators';
import { Wrapper, Img, ButtonGroup, Form, SelectGroup } from './styles-task-opening';
import addUser from '@assets/images/dashboard/target-add-user.svg';
import target_award from '@assets/images/dashboard/target-award.svg';
import statistics from '@assets/images/dashboard/statistics.svg';
import profile from '@assets/images/dashboard/blue-profile1.svg';
import StartToFinish from '@components/common/start-to-finish/start-to-finish';
import { ModalButton, Title } from '@components/Layouts/staff/optional-styles';

const TaskOpening: FC<TaskOpeningProps> = () => {
  const [selectedValue, setSelectedValue] = useState<any>('');
  const [open, setOpen] = useState<boolean>(false)
  const { handleSubmit, control } = useForm<TaskOpeningForm>();
  const handleClose = () =>{
    setOpen(false)
  }
  const onChange = (type: string, value: any) => {
    console.log('type: ', type);
  };
  return (
    <Wrapper>
      <Title>Maqsad yaratish</Title>
      <ButtonGroup>
        <LightButton icon={<Img src={addUser} alt="icon" />} text="Ijrochi" onCLickButton={()=>setOpen(true)}/>
        <HorizontalSeparator width="30px" />
        <LightButton
          icon={<Img src={target_award} alt="icon" />}
          text="Rahbar"
        />
        <HorizontalSeparator width="30px" />
        <LightButton icon={<Img src={profile} alt="icon" />} text="Muallif" />
        <HorizontalSeparator width="30px" />
        <LightButton icon={<Img src={statistics} alt="icon" />} text="Status" />
      </ButtonGroup>
      <Form>
        <Input
          name="targetName"
          placeholder="Nomini kiriting"
          title="Maqsad nomi"
          control={control}
          onChange={onChange}
        />
      </Form>
      <StartToFinish />
      <ButtonGroup>
        <UploadFile />
        <HorizontalSeparator width="30px" />
        <LightButton icon={<Img src={statistics} />} text="Kuzatuvchilar" />
        <HorizontalSeparator width="30px" />
        <LightSelect />
      </ButtonGroup>
      <SelectGroup>
        <Selector
          placeholder="Proyekt tanlash"
          options={['one', 'two', 'three']}
          selectedValue={setSelectedValue}
        />
        <Selector
          placeholder="Kiruvchi vazifalar"
          options={['one', 'two', 'three']}
          selectedValue={setSelectedValue}
        />
        <Selector
          placeholder="Proyekt tanlash"
          options={['one', 'two', 'three']}
          selectedValue={setSelectedValue}
        />
      </SelectGroup>
      <VerticalSeparator height="45px" />
      <div>
        <FormControlLabel
          control={<Switch defaultChecked />}
          label="Kalendarga Qo'shish"
        />
      </div>
      <VerticalSeparator height="45px" />
      <ModalButton
        width="35%"
        color="#fff"
        background="#0496FF"
        padding="18px 0"
      >
        Saqlash
      </ModalButton>
      <UniversalModal open={open } handleClose={handleClose}> <PartnerSelection/> </UniversalModal>
    </Wrapper>
  );
};

export default TaskOpening;
