import { FC, useState } from "react";
import {
  Button,
  DateInput,
  Input,
  LightButton,
  LightSelect,
  MuiSlider,
  Selector,
  TimeInput,
  UniversalModal,
  UploadFile,
} from "@components/common";
import { AddNoteForm, AddNoteModalProps } from "./types-add-note-modal";
import CreateMeeting from "../../../../common/create-meeting/create-meeting";
import {
  AddNoteInputBox,
  AddToCalendar,
  BottomButtonCard,
  BottomSelectorCard,
  ButtonCard,
  Icon,
  NoteTimeInput,
  NoteTimeInputs,
  RepetitionBox,
  SliderInput,
  SubmitButtonBox,
  Title,
  Wrapper,
} from "./styles-add-note-modal";
import StartToFinish from "../../../../common/start-to-finish/start-to-finish";
import closeIcon from "@assets/images/dashboard/chat/close.svg";
import addPerformer from "@assets/images/dashboard/chat/addPerformer.svg";
import LeaderIcon from "@assets/images/dashboard/chat/leader.svg";
import authorIcon from "@assets/images/dashboard/chat/author.svg";
import statusIcon from "@assets/images/dashboard/chat/status.svg";
import sawIcon from "@assets/dashboard/chat/eyeBlue.svg";
import { useForm } from "react-hook-form";
import { styled, Switch, SwitchProps } from "@mui/material";
import RepetitionSelector from "@components/common/repetition-selector/repetition-selector";
const AddNoteModal: FC<AddNoteModalProps> = ({ open, handleClose }) => {
  const [repeatValue, setRepeatValue] = useState<string>("");

  const btnDatas = [
    { icon: addPerformer, title: "Ijrochi" },
    { icon: LeaderIcon, title: "Rahbar" },
    { icon: authorIcon, title: "Muallif" },
    { icon: statusIcon, title: "Status" },
  ];
  const { handleSubmit, control } = useForm<AddNoteForm>({
    defaultValues: {
      meetingName: "",
      comment: "",
      meetingPlace: "",
    },
  });

  return (
    <UniversalModal open={open} handleClose={handleClose}>
      <Wrapper>
        <img src={closeIcon} onClick={handleClose} alt="close icon" />
        <h1>Eslatma qo’shish</h1>
        <AddNoteInputBox>
          <Input
            onChange={() => console.log("clicked")}
            placeholder="Eslatma"
            name="vazifa"
            title="Eslatma nomi"
            control={control}
          />
          <Input
            onChange={() => console.log("clicked")}
            placeholder="Izoh"
            name="vazifa"
            title="Izoh kiriting"
            control={control}
          />
        </AddNoteInputBox>
        <NoteTimeInputs>
          <h2>Eslatma vaqti</h2>
          <NoteTimeInput>
            <TimeInput />
            <DateInput />
          </NoteTimeInput>
        </NoteTimeInputs>
        <RepetitionBox>
          <RepetitionSelector selectedValue={setRepeatValue} />
        </RepetitionBox>
        <SliderInput>
          <Title>Eslatma</Title>
          <MuiSlider />
        </SliderInput>
        <SubmitButtonBox>
          <Button value="Saqlash" type="submit" />
        </SubmitButtonBox>
      </Wrapper>
    </UniversalModal>
  );
};

export default AddNoteModal;
