import { DateInput, DropDownMenu, TimeInput } from '@components/common';
import { FC, useState } from 'react';
import {
  Card,
  Icon,
  InputCard,
  Label,
  Separator,
  Wrapper,
} from './styles-start-to-finish';
import { StartToFinishProps } from './types-start-to-finish';
import pin from '@assets/images/dashboard/pin.svg'
import pin2 from '@assets/images/dashboard/pin-2.svg'

export const PinMenu = [
  {
    icon: pin2,
    title: 'O’zgartira olmaydi',
  },
  {
    icon: pin,
    title: 'O’zgartira oladi',
  },
];
const StartToFinish: FC<StartToFinishProps> = ({hasPin,timeInput}) => {
  const [activePin,setActivePin]= useState('O’zgartira olmaydi')
  const onSelect = (value:string) =>{
    setActivePin(value)
  }
  console.log(activePin);
  return (
    <Wrapper>
      <Card>
        <Label>Boshlanish vaqti</Label>
        <InputCard>
          {/* {timeInput&&<TimeInput />} */}
          <DateInput />
          <Separator width="12" />
        </InputCard>
      </Card>
      <Separator width="40" />
      <Card>
        <Label>Tugatish vaqti</Label>
        <InputCard>
          {/* {timeInput&&<TimeInput />} */}
          <DateInput />
          {!hasPin && <DropDownMenu
            children={<Icon src={activePin==='O’zgartira olmaydi'?pin2:pin} />}
            menuData={PinMenu}
            forNotification={false}
            background="#F4F5F7"
            onSelect={onSelect}
          />}
        </InputCard>
      </Card>
    </Wrapper>
  );
};

export default StartToFinish;
