import React, { FC, useState } from "react";
import { Wrapper } from "./styles-plan-text";
import { PlanTextProps } from "./types-plan-text";
import dotsVertical from "@assets/images/calendar/dots-vertical.svg";
import { Menu, MenuItem } from "@mui/material";
import trashIcon from "@assets/images/dashboard/tasks/trash.svg";
import projectIcon from "@assets/images/calendar/Work.svg";
import checkIcon from "@assets/images/calendar/Accept.svg";
const PlanText: FC<PlanTextProps> = ({ data }) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <Wrapper>
      <p>{data.text}</p>
      <img src={dotsVertical} alt="icon" onClick={handleClick} />
      <Menu
        anchorEl={anchorEl}
        id="account-menu"
        open={open}
        onClose={handleClose}
        onClick={handleClose}
        PaperProps={{
          elevation: 0,
          sx: {
            overflow: "visible",
            filter: "drop-shadow(0px 2px 8px rgba(0,0,0,0.32))",
            mt: 1.5,
            "& .MuiAvatar-root": {
              width: 32,
              height: 32,
              ml: -0.5,
              mr: 1,
            },
            "&:before": {
              content: '""',
              display: "block",
              position: "absolute",
              top: 0,
              right: 14,
              width: 10,
              height: 10,
              bgcolor: "background.paper",
              transform: "translateY(-50%) rotate(45deg)",
              zIndex: 0,
            },
          },
        }}
        transformOrigin={{ horizontal: "right", vertical: "top" }}
        anchorOrigin={{ horizontal: "right", vertical: "bottom" }}
      >
        <MenuItem>
          <img src={projectIcon} alt="icon" /> Proyektga o’tkazish
        </MenuItem>
        <MenuItem>
          <img src={checkIcon} alt="icon" /> Vazifaga o’tkazish
        </MenuItem>
        <MenuItem style={{ color: "#F31B1B" }}>
          <img src={trashIcon} alt="icon" /> O’chirish
        </MenuItem>
      </Menu>
    </Wrapper>
  );
};

export default PlanText;
