import { FC, useEffect, useState } from "react";
import { SearchInputProps } from "./types-search-input";
import { IconButton, InputAdornment } from "@mui/material";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import { Controller } from "react-hook-form";
import { AsTextField, Title, Wrapper, ErrorMsg } from "./styles-search-input";
import search from "@assets/images/dashboard/dashboard-navbar/Search.svg";
const SearchInput: FC<SearchInputProps> = ({ placeholder, name, onChange }) => {
  return (
    <Wrapper>
      <AsTextField
        fullWidth
        placeholder={placeholder}
        variant="outlined"
        type={"text"}
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <IconButton
                aria-label="toggle password visibility"
                onClick={() => console.log("searchbtn")}
                edge="end"
              >
                <img src={search} alt="search" />
              </IconButton>
            </InputAdornment>
          ),
        }}
      />
    </Wrapper>
  );
};

export default SearchInput;
