import { FC, useEffect } from "react";
import { sidebarAssets } from "../../../data/data";
import { Icon, Wrapper } from "./styles-sidebar";
import { SidebarProps } from "./types-sidebar";
import logo from "@assets/images/dashboard/LOGO.svg";
import { useHistory, useParams } from "react-router-dom";

const Sidebar: FC<SidebarProps> = ({ activeState, setActiveState }) => {
  const history = useHistory();
  const params = useParams();
  // console.log('params:', params);

  const Route = (index: number) => {
    switch (index) {
      case 0:
        return history.push("/chats");
      case 1:
        return history.push("/calendar");
      case 2:
        return history.push("/tasks");
      case 3:
        return history.push("/meetings");
      case 4:
        return history.push("/staff");
      case 5:
        return history.push("/suggestion");
    }
  };
  return (
    <Wrapper>
      <Icon
        src={logo}
        active={false}
        onClick={() => history.push("/companies")}
      />
      {sidebarAssets.map((item, index) => (
        <Icon
          key={index}
          src={activeState === index ? item.active_icon : item.icon}
          active={activeState === index}
          onClick={() => {
            Route(index);
            setActiveState(index);
            window.localStorage.setItem("lastPanelId", index.toString());
          }}
        />
      ))}
    </Wrapper>
  );
};

export default Sidebar;
