import { FC } from "react";
import { Button } from "./styles-light-button";
import { LightButtonProps } from "./types-light-button";

const LightButton: FC<LightButtonProps> = ({
  icon,
  text,
  style,
  score,
  onCLickButton,
}) => {
  return (
    <Button onClick={onCLickButton} style={style}>
      {icon}{" "}
      <p>
        {text} {score && <span>{score}</span>}
      </p>
    </Button>
  );
};

export default LightButton;
