import { FC } from "react";
import { Icon, Label, Upload, WrapIcon } from "./styles-upload-file";
import note from "@assets/images/dashboard/note.svg";

const UploadFile: FC<{}> = () => {
  return (
    <Label>
      Fayllar
      <Upload type="file" />
      <WrapIcon>
        <Icon src={note} />
        <p>2</p>
      </WrapIcon>
    </Label>
  );
};

export default UploadFile;
