import { FC, useState } from 'react';
import TextField from '@mui/material/TextField';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { StaticDatePicker } from '@mui/x-date-pickers/StaticDatePicker';
import {
  Button,
  ButtonGroup,
  Description,
  From,
  FromToCard,
  SelectionDate,
  Wrapper,
} from './styles-calendar';
import { differentInDays, formatDate } from '@src/utils/functions';
import { toast } from 'react-toastify';

const Calendar: FC<{}> = () => {
  const [from, setFrom] = useState<Date>(new Date());
  const [to, setTo] = useState<Date>(new Date());
  const [fromActive, setFromActive] = useState<boolean>(false);
  const setTime = (date: Date) => {
    if (fromActive) {
      date?.getTime() < to.getTime()
        ? setFrom(date)
        : toast.error('Tanlashda xatolik');
    } else {
      date?.getTime() > from.getTime()
        ? setTo(date)
        : toast.error('Tanlashda xatolik');
    }
  };
  return (
    <Wrapper>
      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <SelectionDate>
          <FromToCard>
            <From active={fromActive} onClick={() => setFromActive(true)}>
              {formatDate(from)}
            </From>
            <From active={!fromActive} onClick={() => setFromActive(false)}>
              {formatDate(to)}
            </From>
          </FromToCard>
          <Description>
            Tanlangan davrning davomiyligi: {differentInDays(from, to)} kun
          </Description>
        </SelectionDate>
        <StaticDatePicker
          displayStaticWrapperAs="desktop"
          openTo="day"
          value={fromActive ? from : to}
          onChange={(newValue) => {
            //@ts-ignore
            setTime(newValue);
          }}
          renderInput={(params) => <TextField {...params} />}
        />
        <ButtonGroup>
          <Button>Bekor qilish</Button>
          <Button colored>Tanlash</Button>
        </ButtonGroup>
      </LocalizationProvider>
    </Wrapper>
  );
};

export default Calendar;
