import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import { FC, useState } from "react";
import { LightSelectProps } from "./types-light-select";
import { Icon, Wrapper } from "./styles-light-select";
import down_icon from "@assets/images/dashboard/blue-down-icon.svg";
import red_flag from "@assets/images/dashboard/red-flag.svg";
import yellow_flag from "@assets/images/dashboard/yellow-flag.svg";
import green_flag from "@assets/images/dashboard/green-flag.svg";

const LightSelect: FC<LightSelectProps> = () => {
  const [value, setValue] = useState(10);

  const handleChange = (event: any) => {
    console.log("value: ", event.target.value);
    setValue(event.target.value);
  };

  return (
    <Wrapper>
      <FormControl fullWidth>
        <p>Muhimlilik</p>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={value}
          onChange={handleChange}
          IconComponent={() => <Icon src={" "} />}
        >
          <MenuItem selected value={10}>
            <img src={red_flag} alt="yuqori" />
            Yuqori
          </MenuItem>
          <MenuItem value={20}>
            <img src={yellow_flag} alt="'o'rta'" />
            O'rata
          </MenuItem>
          <MenuItem value={30}>
            <img src={green_flag} alt="past" />
            Past
          </MenuItem>
        </Select>
      </FormControl>
    </Wrapper>
  );
};

export default LightSelect;
