import { FC, useEffect, useState } from "react";
import Autocomplete from "@mui/material/Autocomplete/Autocomplete";
import TextField from "@mui/material/TextField/TextField";
import {
  RepetitionSelectorProps,
  WeekDaysProps,
} from "./types-repetition-selector";
import {
  Day,
  MonthSpecialDay,
  SelectorBox,
  Title,
  WeekDays,
  Wrapper,
} from "./styles-repetition-selector";
import DropDownIcon from "@assets/images/common/selector/dropdown.svg";
import {
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
} from "@mui/material";
const RepetitionSelector: FC<RepetitionSelectorProps> = ({
  selectedValue,
  withClearIcon,
}) => {
  const options = ["Bir marta", "Har kuni", "Har hafta", "Har oy", "Har yili"];
  const [weekDays, setWeekDays] = useState<WeekDaysProps[]>([
    { title: "Du", value: "Dushanba", active: false },
    { title: "Se", value: "Seshanba", active: false },
    { title: "Ch", value: "Chorshanba", active: false },
    { title: "Pa", value: "Payshanba", active: false },
    { title: "Ju", value: "Juma", active: false },
    { title: "Sh", value: "Shanba", active: false },
  ]);
  const [value, setValue] = useState("Bir marta");
  const [repeatValue, setRepeatValue] = useState<string>("");
  const chooseRepetitionDay = (idx: number) => {
    let days = [...weekDays];
    let day = { ...weekDays[idx] };
    day.active = !day.active;
    days[idx] = day;
    setWeekDays(days);
  };
  return (
    <Wrapper>
      <SelectorBox>
        <Title>Takroriyligi</Title>
        <Autocomplete
          popupIcon={<img src={DropDownIcon} alt="Drop Down" />}
          disablePortal
          clearIcon={!withClearIcon}
          id="combo-box-demo"
          value={value}
          onChange={(event: any, value: any) => {
            selectedValue(value);
            setRepeatValue(value);
            setValue(value);
          }}
          options={options}
          sx={{ width: 300 }}
          renderInput={(params) => <TextField {...params} />}
        />
      </SelectorBox>
      {repeatValue === "Har hafta" ? (
        <WeekDays>
          {weekDays.map((day, idx) => (
            <Day
              onClick={() => chooseRepetitionDay(idx)}
              className={day.active ? "active" : ""}
            >
              <p>{day.title}</p>
            </Day>
          ))}
        </WeekDays>
      ) : repeatValue === "Har oy" ? (
        <MonthSpecialDay>
          <FormControl>
            <RadioGroup
              row
              aria-labelledby="demo-row-radio-buttons-group-label"
              name="row-radio-buttons-group"
            >
              <FormControlLabel
                value="female"
                control={<Radio />}
                labelPlacement="start"
                label="Har oyning 27-sanasida"
              />
              <FormControlLabel
                value="other"
                control={<Radio />}
                labelPlacement="start"
                label="Har oyning 4-chorshanbasida"
              />
              <FormControlLabel
                labelPlacement="start"
                value="disabled"
                control={<Radio />}
                label="Har oying oxirgi chorshanbasida"
              />
            </RadioGroup>
          </FormControl>
        </MonthSpecialDay>
      ) : (
        ""
      )}
    </Wrapper>
  );
};

export default RepetitionSelector;
