import styled from 'styled-components';

export const Card = styled.div`
  display: flex;
  justify-content: flex-start;
`;

export const Title = styled.h1`
  font-weight: 600;
  font-size: 15px;
  line-height: 22px;
  /* display: flex; */
  color: #000000;
  margin-left: 12px;
`;

export const Label = styled.a`
  font-weight: 500;
  font-size: 15px;
  line-height: 22px;
  color: #000000;
  margin-left: 10px;
`;

export const Time = styled.a`
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  color: #a5a9b8;
  margin-left: 10px;
`;

export const ButtonGroup = styled.div`
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
`;
